
@charset "UTF-8";
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
  zoom: 1;
}
.clearfix:after {
    content: " ";
    display: block;
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.vux-loading .weui-toast {
  padding: 0px 10px !important;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.index_alert {
  position: fixed;
  width: 2rem;
  top: calc(50% - 02rem);
  z-index: 999;
  /*min-height: 1rem;*/
  left: calc(50% - 1.4rem);
  text-align: center;
  font-size: 0.32rem;
  line-height: 0.4rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  /*font-size: 1.07692em;*/
  padding: 0.615385em 1.23077em;
  border-radius: 0.307692em;
  -webkit-box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 0.61538em;
          box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 0.61538em;
}
