#app .tabwrap {
    width: 100%;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f4f4f4;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 45px;
    padding-top: 5px;
    z-index: 99;
}
#app .tabwrap:before {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      border-top: 1px solid #eee;
      content: ' ';
}
#app .tabwrap .tab-item {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      text-align: center;
}
#app .tabwrap .tab-item .icon {
        width: 0.48rem;
        height: 0.48rem;
        background-size: 100% auto;
        background-repeat: no-repeat;
        margin: 0 auto;
}
