#app .fade-enter-active, #app .fade-leave-active {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
#app .fade-enter, #app .fade-leave-to {
    opacity: 0;
}
#app .slide-fade-enter-active {
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}
#app .slide-fade-leave-active {
    -webkit-transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
    transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}
#app .slide-fade-enter, #app .slide-fade-leave-to {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
    opacity: 0;
}
#app .navigation {
