@font-face {
    font-family: "zhengwen";
    src: url("zhengwen.eot"); /* IE9 */
    src: url("zhengwen.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    
    url("zhengwen.woff") format("woff"), /* chrome、firefox */
    url("zhengwen.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+ */
    
    url("zhengwen.svg#zhengwen") format("svg"); /* iOS 4.1- */
    font-style: normal;
    font-weight: normal;
}





