}
#recruitGame .swiperBox .waiyuanA {
      -webkit-animation: waiyuanAItem 10s linear infinite;
              animation: waiyuanAItem 10s linear infinite;
}
#recruitGame .swiperBox .waiyuanA02 {
      -webkit-animation: waiyuanAItem 3s linear infinite;
              animation: waiyuanAItem 3s linear infinite;
}
#recruitGame .swiperBox .shanzhuan1 {
      -webkit-animation: shanzhuan 2s linear alternate infinite;
