}
#recruitGame .swiperBox .shanzhuan2 {
      -webkit-animation: shanzhuan 2s linear alternate infinite;
              animation: shanzhuan 2s linear alternate infinite;
      -webkit-animation-delay: 1.5s;
              animation-delay: 1.5s;
}
@-webkit-keyframes waiyuanAItem {
0% {
    -webkit-transform: rotateX(112deg) rotate(0deg);
            transform: rotateX(112deg) rotate(0deg);
}
25% {
    -webkit-transform: rotateX(112deg) rotate(90deg);
            transform: rotateX(112deg) rotate(90deg);
}
50% {
    -webkit-transform: rotateX(112deg) rotate(180deg);
            transform: rotateX(112deg) rotate(180deg);
}
75% {
    -webkit-transform: rotateX(112deg) rotate(270deg);
            transform: rotateX(112deg) rotate(270deg);
}
100% {
    -webkit-transform: rotateX(112deg) rotate(360deg);
            transform: rotateX(112deg) rotate(360deg);
}
}
@keyframes waiyuanAItem {
0% {
    -webkit-transform: rotateX(112deg) rotate(0deg);
            transform: rotateX(112deg) rotate(0deg);
}
25% {
    -webkit-transform: rotateX(112deg) rotate(90deg);
            transform: rotateX(112deg) rotate(90deg);
}
50% {
    -webkit-transform: rotateX(112deg) rotate(180deg);
            transform: rotateX(112deg) rotate(180deg);
}
75% {
    -webkit-transform: rotateX(112deg) rotate(270deg);
            transform: rotateX(112deg) rotate(270deg);
}
100% {
    -webkit-transform: rotateX(112deg) rotate(360deg);
            transform: rotateX(112deg) rotate(360deg);
}
}
@-webkit-keyframes diquiZhuanA {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes diquiZhuanA {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes shanzhuan {
0% {
    opacity: 0.2;
    /*-webkit-transform: rotate(0deg);*/
}
25% {
    opacity: 0.4;
}
50% {
    opacity: 0.6;
}
75% {
    opacity: 0.8;
}
100% {
    opacity: 0.9;
    /*-webkit-transform: rotate(45deg);*/
}
}
