}
}
@-webkit-keyframes shanzhuanXX {
0% {
    opacity: 0.2;
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9);
    /*-webkit-transform: rotate(0deg);*/
}
