          font-size: 1.7rem;
          font-weight: bold;
}
#recruitGame .swiperBox .page2-boxitem .year b {
          font-size: 0.6rem;
          margin-left: 0.2rem;
}
#recruitGame .swiperBox .page2-boxitem .title {
        font-size: 0.31rem;
        line-height: 1rem;
        padding: 0 0.8rem;
        /*padding-top: 0.5rem;*/
}
#recruitGame .swiperBox .page2-boxitem .left {
        text-align: left;
}
#recruitGame .swiperBox .page2-boxitem .startGameBg {
        height: 0.35rem;
        position: absolute;
        bottom: 1rem;
        width: 3.3rem;
        padding: 0.25rem 0rem;
        right: calc(50% - 1.6rem);
        z-index: 1;
        /* Safari 5.1 - 6.0 */
        /* Opera 11.1 - 12.0 */
        /* Firefox 3.6 - 15 */
        background: -webkit-gradient(linear, left top, right top, from(#636caf), to(rgba(255, 255, 255, 0)));
        background: linear-gradient(to right, #636caf, rgba(255, 255, 255, 0));
        /* 标准的语法 */
}
#recruitGame .swiperBox .page2-boxitem .startGame {
        z-index: 2;
        font-size: 0.35rem;
        position: absolute;
        bottom: 1rem;
        width: 2.4rem;
        padding: 0.25rem 0rem;
        right: calc(50% - 1.3rem);
        background: #044355;
        -webkit-background-clip: text;
        /*按文字裁剪*/
        -webkit-text-fill-color: transparent;
        /*文字的颜色使用背景色*/
        background-color: #19385c;
        /*设置一个背景色*/
        background-image: -webkit-linear-gradient(-45deg, rgba(0, 0, 0, 0.6) 30%, #aff0ff 50%, rgba(0, 0, 0, 0.6) 70%);
        /*设置渐变的背景，按对角线渐变*/
        background-blend-mode: hard-light;
        /*设置背景为混合模式下的强光模式*/
        background-size: 200%;
        -webkit-animation: shine 2s infinite;
                animation: shine 2s infinite;
        /*给背景添加动画改变位置*/
        font-weight: bold;
}
@-webkit-keyframes shine {
from {
    background-position: 150%;
}
to {
    background-position: -20%;
}
}
@keyframes shine {
from {
    background-position: 150%;
}
to {
    background-position: -20%;
}
}
