@-webkit-keyframes bounce-in {
0% {
    opacity: 0;
    -webkit-transform: translate(-45px);
            transform: translate(-45px);
}
50% {
    opacity: 0.3;
}
100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px);
}
}
@keyframes bounce-in {
0% {
    opacity: 0;
    -webkit-transform: translate(-45px);
            transform: translate(-45px);
}
50% {
    opacity: 0.3;
}
100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px);
}
}
@-webkit-keyframes bounce-in_r {
0% {
    opacity: 0;
    -webkit-transform: translate(45px);
            transform: translate(45px);
}
50% {
    opacity: 0.3;
}
100% {
